<template>
  <div class="hs-container">
    <c-header isBackNav :title="$t('base.FrequentContactsManagement')" fixedBackPath="/management"></c-header>
    <hs-scroll
      class="table-view"
      ref="tableView"
      :data="datalist"
      :options="tableViewOptions"
      @pulling-down="_getDatalist">
      <ul>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-buttons">
              <hs-button class="button" @click="_gotoNew">{{$t('base.Add')}}</hs-button>
              <hs-button class="button" type="common" @click="_gotoAdd">{{$t('base.Import')}}</hs-button>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-1 v-for="(item, index) in datalist" :key="index" @click.native="_gotoDetails(item)">
            <div class="custom-cell-contact">
              <div class="details">
                <div class="item">
                  <div class="name">
                    <p>{{item.Name}}</p>
                    <p class="mine" v-if="item.IsCurrentLoginUser">{{$t('base.Self')}}</p>
                    <p class="default" v-if="item.IsDefault">{{$t('base.Default')}}</p>
                    <p class="frequent" v-if="item.ContactUpID">{{$t('base.FrequentTraveler')}}</p>
                  </div>
                </div>
                <div class="item">
                  <p>{{$t('base.CellphoneNumber')}}</p>
                  <p>{{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                </div>
                <div class="item">
                  <p>{{$t('base.Email')}}</p>
                  <p>{{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsButton from 'base/hs-button/hs-button'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import {kpiUserGetDefaultContacts} from 'api'
export default {
  created() {
    this._getDatalist()
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      datalist: []
    }
  },
  methods: {
    _getDatalist() {
      kpiUserGetDefaultContacts().then((res) => {
        console.log('获取常用联系人列表: ', res)
        this.datalist = res.ResultData
        // 将本人挪至第一位
        let mine = this.datalist.filter(v => v.IsCurrentLoginUser === true)
        if (mine && mine[0]) {
          let index = this.datalist.map(v => v.ID).indexOf(mine[0].ID)
          this.datalist.splice(index, 1)
          this.datalist.unshift(mine[0])
        }
        // =============================
      })
    },
    _gotoNew() {
      this.$router.push({path: '/personal/newContact'})
    },
    _gotoAdd() {
      this.$router.push({name: 'personalAddContacts'})
    },
    _gotoDetails(item) {
      this.$router.push({path: '/personal/frequentContactDetails', query: {details: JSON.stringify(item)}})
    }
  },
  components: {
    CHeader,
    HsButton,
    HsScroll,
    HsSection,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .custom-cell-buttons
      @extend .flex-row
      @extend .flex-center
      width 100%
      padding 12px 0
      >.button
        margin 0 5px
        flex 1
        // letter-spacing 0.5em
        &:first-child
          margin-left 0px
        &:last-child
          margin-right 0px
    .custom-cell-contact
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 12px 0
      >.details
        width 100%
        >.item
          @extend .flex-row
          @extend .flex-start
          width 100%
          >.name
            @extend .flex-row
            @extend .flex-start
            width 100% !important
            font-size 0.9rem
            color $color_black_text_0
            margin-bottom 5px
            flex-wrap wrap
            >p
              font-weight bold
              margin-right 5px
              line-height 1.2em
            >.frequent
            >.mine
            >.default
              font-size 0.7rem
              margin-right 5px
              border 1px solid $color_theme
              background-color $color_red_background_0
              color $color_theme
              border-radius 3px
              padding 2px 5px
              font-weight normal
            >.frequent
              border 1px solid $color_green_layer !important
              color $color_green_layer !important
              background-color $color_green_background_0 !important
            >.mine
              border 1px solid $color_orange_layer !important
              color $color_orange_layer !important
              background-color $color_orange_background_0 !important
          >p
            font-size 0.8rem
            line-height 1.5em
            color $color_gray_text
            &:nth-child(1)
              width 60px
              margin-right 10px
            &:nth-child(2)
              width calc(100% - 70px)
</style>



